* {
  margin: 0;
  padding: 0;
}

.navbar {
  position: sticky;
  z-index: 3;
  top: 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
  background-color: #fff;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.linkitems {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  list-style: none;
}

li {
  margin: 0 0.5rem;
  font-size: 1.2rem;
  padding: 0.3rem;
  border-radius: 0.5rem;
  cursor: pointer;
}

.logo img {
  height: 4rem;
  margin: 0.3rem;
}

li a.active {
  /* font-weight: bold; */
  color: rgb(37, 115, 199);
}

.navmobmenu {
  position: absolute;
  top: 4rem;
  right: 2rem;
  z-index: 2;
  display: flex;
  flex-direction: column;
  padding: 0.2rem;
  height: fit-content;
  min-width: 10rem;
  background: rgb(40, 40, 40);
  border-radius: 1rem;
  align-items: center;
  justify-content: center;
}

.mobmenuitem {
  color: white;
  /* padding: 0.5rem 3rem; */
  padding: 0.25rem;
  cursor: pointer;
  /* background: rgb(30, 30, 30); */
}

.mobmenu {
  display: none;
  object-fit: cover;
  height: 2rem;
}

@media screen and (max-width: 720px) {
  .logo img {
    height: 2rem;
    margin: 0.3rem;
  }

  .navbar {
    height: 3rem;
  }
  .mobmenu {
    display: flex;
    height: 1.5rem;
  }
  .links {
    display: none;
  }
  .linkitems {
    display: none;
  }
}
