.scroll-container {
  display: flex;
  flex-direction: column;
  /* width: 80%; */
  overflow-x: auto;
  white-space: nowrap;
  /* border: 2px solid #ccc; */
  padding: 10px;
  background-color: #32a7e2;
  justify-content: center;
  align-items: center;
}

.scroll-content {
  display: inline-flex;
}

.client {
  display: inline-block;
  margin-right: 20px;
  padding: 1rem;
}

.client img {
  max-width: 100px;
  height: 100px;
  border: 1px solid gray;
}

@media screen and (max-width: 480px) {
  .client {
    font-size: 1.5rem;
    font-weight: bold;
  }
}
