.about {
  justify-content: center;
  align-items: center;
  margin-bottom: 1rem;
}

.header1 {
  margin-top: 2rem;
  text-align: center;
}
.content {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-top: 1rem;
}

.left {
  width: 45%;
  padding: 0.5rem;
  border-right: 2px solid black;
}

.leftcontent {
  font-size: 1.2rem;
  text-align: left;
  height: 100%;
}

.rightcontent {
  font-size: 1.2rem;
  text-align: left;
  margin-left: 1rem;
}

.right {
  width: 40%;
  padding: 0.5rem;
}

.rightheader {
  font-size: 1.5rem;
  font-weight: 800;
  margin-bottom: 0.5rem;
}

@media screen and (max-width: 480px) {
  .header1 {
    font-size: 1.3rem;
    text-align: center;
  }
  .content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 0 0.5rem;
  }

  .left {
    width: fit-content;
    padding: 0.7rem;
    border: none;
  }

  .right {
    width: fit-content;
    padding: 0.5rem;
  }

  .rightheader {
    font-size: 1.3rem;
    text-align: center;
    /* margin-bottom: 0.5rem; */
  }

  .leftcontent {
    font-size: 0.9rem;
    text-align: left;
    padding: 0.3rem;
  }
  .rightcontent {
    font-size: 0.9rem;
    text-align: left;
  }
}
