.footer {
  /* width: 100vw; */
  /* height: 4rem; */
  background: rgb(6, 172, 227);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  font-size: medium;
  font-weight: 300;
}

.footerlinks {
  display: flex;
  flex-direction: row;
  /* align-items: center; */
  justify-content: center;
  text-align: center;
  font-size: medium;
  font-weight: 300;
}

.footerheading {
  font-size: large;
  font-weight: 500;
  margin: 0.5rem;
  text-align: center;
}

.Our-Offices {
  margin: 1rem;
  width: 20rem;
  text-align: left;
}

.footercontact {
  margin: 1rem;
  width: 15vw;
  text-align: left;
}

.ourchemicals {
  margin: 1rem;
  width: 15vw;
  text-align: left;
}

.copyright {
  border-top: 1px solid black;
  padding: 1rem;
}

.links {
  margin: 1rem;
  display: flex;

  flex-direction: column;
}

.link {
  object-fit: cover;
  height: 2.2rem;
  width: 2.2rem;
  /* margin: 0.5rem 0.5rem; */
}

.linklinkedin {
  object-fit: cover;
  height: 2.7rem;
  width: 2.68rem;
  margin: 0.7rem 0.6rem;
}

.linkgithub {
  object-fit: cover;
  height: 3.5rem;
  width: 3.7rem;
  margin-top: 0.2rem;
}

@media screen and (max-width: 480px) {
  .footerlinks {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    text-align: center;
    font-size: 0.9rem;
    font-weight: 300;
    margin: 0.5rem;
  }

  .footerheading {
    font-size: 1.2rem;
    font-weight: bold;
    margin: 0.5rem;
    text-align: center;
  }

  .Our-Offices {
    margin: 0.5rem;
    width: fit-content;
    text-align: left;
  }

  .footercontact {
    margin: 0.5rem;
    width: fit-content;
    text-align: center;
  }

  .ourchemicals {
    margin: 0.5rem rem;
    width: 80%;
    text-align: center;
  }

  .copyright {
    border-top: 1px solid black;
    padding: 1rem;
  }

  .links {
    margin: 0.5rem;
    display: flex;
    width: fit-content;
    flex-direction: column;
  }

  .link {
    object-fit: cover;
    height: 2.2rem;
    width: 2.2rem;
    /* margin: 0.5rem 0.5rem; */
  }
}
