.service h1 {
  text-align: center;
  margin-top: 1rem;
}

.sevicescards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  padding: 2rem;
  margin: 0 2rem;
}

.sevicescard {
  display: flex;
  flex-direction: column;
  width: 15rem;
  height: 15rem;
  margin: 1.5rem;
  border-radius: 10px;
  padding: 1rem;
  align-items: center;
  box-shadow: rgb(150, 148, 148) 0.1rem 0.1rem 0.1rem 0.1rem;
}

.sevicescardimg img {
  width: 2rem;
  margin: 1rem 0;
}

.servicecardcontentheader {
  text-align: center;
  font-weight: bold;
  margin-bottom: 0.5rem;
}

@media screen and (max-width: 480px) {
  .service h1 {
    text-align: center;
    font-size: 1.3rem;
  }

  .sevicescards {
    padding: 0.5rem;
    margin: 0 0.5rem;
  }

  .sevicescard {
    width: fit-content;
    height: fit-content;
    margin: 1.5rem;
    border-radius: 10px;
    padding: 1rem;
    align-items: center;
    box-shadow: rgb(150, 148, 148) 0.1rem 0.1rem 0.1rem 0.1rem;
  }

  .sevicescardimg img {
    width: 2rem;
    margin-bottom: 0.5rem;
  }

  .servicecardcontentheader {
    text-align: center;
    font-weight: bold;
    margin-bottom: 0.5rem;
  }

  .sevicescardcontent p {
    font-size: 0.9rem;
  }
}
