.title {
  text-align: center;
  padding-top: 1rem;
}

.product {
  background-color: rgb(56, 171, 228);
}

.cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  padding: 2rem;
  margin: 0 2rem;
}

.card {
  display: flex;
  flex-direction: column;
  width: fit-content;
  margin: 1.5rem;
  /* border: 2px solid black; */
  border-radius: 10px;
  padding: 1rem;
}

.cardimg img {
  width: 18rem;
  height: 13rem;
  margin-bottom: 1rem;
}

@media screen and (max-width: 480px) {
  .title {
    font-size: 1.3rem;
    text-align: center;
    padding-top: 1rem;
  }

  .product {
    background-color: rgb(56, 171, 228);
  }

  .cards {
    padding: 0.5rem;
  }

  .card {
    display: flex;
    flex-direction: column;
    width: fit-content;
    margin: 0.5rem;
    /* border: 2px solid black; */
    border-radius: 10px;
    padding: 1rem;
  }

  .cardimg img {
    width: 16rem;
    height: 11rem;
    margin-bottom: 0.5rem;
    border-radius: 0.5rem;
  }

  .cardcontent {
    text-align: center;
    font-size: 0.9rem;
  }
}
